import React, { Component } from 'react'
import NewsItem from './NewsItem'
import Spinner from './Spinner';
import PropTypes from 'prop-types'


export class News extends Component {
    static defaultProps = {
        country: "in",
        pageSize: 15,
        category: "general"
    } 
    static PropsTypes = {
        country: PropTypes.string,
        pageSize: PropTypes.number,
        category: PropTypes.string
    }
    cap = (string)=>{
        return string.charAt(0).toUpperCase() + string.slice(1);
    }
    articles = []
    constructor(props) {
        super(props);
        console.log('hey');
        this.state = {
            articles: this.articles,
            loading: false,
            page: 1,
        }
        document.title = `NewsTImes - ${this.cap(this.props.category)}`;
    }

    async componentDidMount() {
        let UrL = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=cba3e053364949ce82ba84347596b24b&page=1&pageSize=${this.props.pageSize}`;
         this.setState({loading: true});
        let data = await fetch(UrL);
        let parsedData = await data.json();
        this.setState({loading: false});
        this.setState({ articles: parsedData.articles, totalResults: parsedData.totalResults})

    }
        HandlePrevClick = async ()=>{
            console.log("Previous Click")
            let UrL = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=cba3e053364949ce82ba84347596b24b&page=${this.state.page-1}&pageSize=${this.props.pageSize}`;
            this.setState({loading: true});         
            let data = await fetch(UrL);
            let parsedData = await data.json();
             this.setState({
                 page: this.state.page - 1,
                 articles: parsedData.articles,
                 loading: false,
             })
        }
        HandleNextClick = async ()=>{
            console.log("next click")
            if(!(this.state.page+1>Math.ceil(this.state.totalResults/9))){
            let UrL = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=cba3e053364949ce82ba84347596b24b&page=${this.state.page+1}&pageSize=${this.props.pageSize}`;
            this.setState({loading: true});
            let data = await fetch(UrL);
            let parsedData = await data.json();
             this.setState({
                 page: this.state.page + 1,
                 articles: parsedData.articles,
                 loading: false,
              })
        }}

    render() {
        return (
            <div className='container my-3'>
              <h2 className="text-center"> NewsTimes - Top {this.cap(this.props.category)} Headlines</h2>
                {this.state.loading && <Spinner/>}
                <div className='row'>
                    {!this.state.loading && this.state.articles.map((element) => {
                        return <div className='col-md-4 my-3' key={element.url}>
                            <NewsItem title={element.title ? element.title.slice(0, 40):""} description={element.description ? element.description.slice(0, 88): ""} imageUrl={element.urlToImage} newsUrl={element.url} date={element.publishedAt}/>
                        </div>
                    })}
                </div>
                <div className="container d-flex justify-content-between">
                <button disabled={this.state.page<=1} type="button" onClick={this.HandlePrevClick} className="btn btn-dark pn">&larr; Previous</button>
                <button disabled={this.state.page+1>Math.ceil(this.state.totalResults/9)} onClick={this.HandleNextClick} type="button" className="btn btn-dark pn">Next &rarr;</button>
                </div>
            </div>
        )
    }
}

export default News
