import './App.css';
import React, { Component } from 'react'
import NavBar from './components/NavBar';
import News from './components/News';
import {
  HashRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
export class App extends Component {
  render() {
    return (
      <div>
        <Router>
        <NavBar/>
        <Switch>
       <Route exact path="/"> <News key="general" pageSize={this.props.nummber} country="in" category="general"/></Route>
       <Route exact path="/sports"> <News key="sports" pageSize={this.props.number} country="in" category="sports"/></Route>
       <Route exact path="/entertainment"> <News key="entertainment" pageSize={this.props.number} country="in" category="entertainment"/></Route>
       <Route exact path="/business"> <News key="business" pageSize={this.props.number} country="in" category="business"/></Route>
       <Route exact path="/health"> <News key="health" pageSize={this.props.number} country="in" category="health"/></Route>
       <Route exact path="/science"> <News key="science" pageSize={this.props.number} country="in" category="science"/></Route>
       <Route exact path="/technology"> <News key="technology" pageSize={this.props.number} country="in" category="technology"/></Route>
       <Route exact path="/worldNews"> <News key="worldNews" pageSize={this.props.number} country="us" category="general"/></Route>
        </Switch>
        </Router>
      </div>
    )
  }
}

export default App
