import React, { Component } from 'react'

export class NewsItem extends Component {
    render() {
        let { title, description, imageUrl, newsUrl, date } = this.props;
        return (
            <div className='my-3'>
                <div className="" style={{ width: "13rem" }}>
                <h6><span class="badge bg-danger">New</span></h6>
                    <img className="card-img-top cardImg" src={!imageUrl ?
         "https://image.shutterstock.com/image-vector/no-image-available-icon-flat-600w-1240855801.jpg" : imageUrl} 
         alt="Card image cap" /> 
                    <div className="card-body">
                        <h5 className="card-title">{title}...</h5>
                        <p className="card-text">{description}...</p>
                        <p className="card-text"><small class="text-muted">Last updated on {new Date(date).toGMTString()}</small></p>
                        <a href={newsUrl} target="_blank" className="btn btn-dark hey">Read More</a>
                    </div>
                </div>

            </div>
        )
    }
}

export default NewsItem 